<template>
  <div class="swiper-box">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          v-for="(item,index) in list"
          class="swiper-slide"
          :key="index"
          :style="`background:url(${item.img})`"
        ></div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  mounted() {
    //mountedlistswiper-slideswiper
    this.init();
  },
  updated() {
    this.init();
  },
  created() {
    if (this.list.length) return; //vuex
    this.$store.dispatch("getBanner");
  },
  computed: {
    //vuexcomputed
    list() {
      return this.$store.state.bannerList;
    }
  },
  methods: {
    init() {
      new Swiper(".swiper-container", {
        loop: true,
        pagination: {
          el: ".swiper-pagination"
        },
        speed:500,
        autoplay : {
          delay:1000
        },
      });
    }
  }
};
</script>

<style lang="less" scoped>
.swiper-container {
  width: 90%;
  height: 3rem;
  border-radius: 10px;
  margin-top: 0.2rem;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
}
.swiper-box {
  width: 100%;
  position: relative;
  height: 100%;
}
.swiper-slide {
  background-size: cover !important;
}
</style>
