<template>
  <div class="hot-book">
    <h2></h2><span class="more" @click='fn'>  ></span>
    <div class="list_box">
      <list v-for='(item,index) in list' :key='index' :img='item.img' :title='item.title'></list>
    </div>
  </div>
</template>

<script>
import list from './list'
export default {
    data() {
        return {
            list:[]
        }
    },
    components: {
        list,
    },
    created () {
        this.$store.dispatch('getHomeList').then((data) => {
            this.list = data.data
        })
    },
    methods: {
      fn() {
        this.$router.push('/list')
      }
    },
};
</script>

<style lang="less" scoped>
.hot-book {
  position: relative;
  top: 0.2rem;
  h2 {
    color: #333;
    font-size: 0.4rem;
    width:90%;
    margin: 0 auto 0.2rem;
  }
}
.list_box {
  width: 100%;
  overflow: hidden;
  
}
.more{
  font-size:0.16rem;
  position: absolute;
  right:0.4rem;
  top:0.1rem;
  color:#999;
  white-space:pre-wrap;
}
</style>